<template>
  <section>
    <yd-slider autoplay="3000">
      <yd-slider-item>
        <img src="../common/slider/slider_1.jpg"/>
      </yd-slider-item>
      <yd-slider-item>
        <img src="../common/slider/slider_2.jpg"/>
      </yd-slider-item>
      <yd-slider-item>
        <img src="../common/slider/slider_3.jpg"/>
      </yd-slider-item>
    </yd-slider>

    <section class="grids-container">
      <section class="grids-item-container">
        <router-link :to="{path:'/subway'}" class="grids-item" tag="section">
          <section class="grids-icon-container" style="background-color: #04BE02;">
            <yd-icon name="subway" size=".8rem" color="#fff" custom></yd-icon>
          </section>

          <section class="grids-text-container">城市地铁</section>
        </router-link>

        <router-link :to="{path:'/toilet'}" class="grids-item" tag="section">
          <section class="grids-icon-container" style="background-color: brown;">
            <yd-icon name="toilet" size=".8rem" color="#fff" custom></yd-icon>
          </section>

          <section class="grids-text-container">公厕在哪</section>
        </router-link>

        <section class="grids-item">
          <section class="grids-icon-container" style="background-color: #5B92EC;">
            <yd-icon name="tianqi" size=".8rem" color="#fff" custom></yd-icon>
          </section>

          <section class="grids-text-container">城市天气</section>
        </section>
      </section>
    </section>
  </section>
</template>

<script type="text/ecmascript-6">
  export default {
    name: 'home'
  }
</script>

<style type="text/css" scoped>

  .grids-container >>> .grids-item-container .grids-text-container {
    font-size: 0.26rem;
  }

  .grids-container >>> .grids-item-container .grids-icon-container {
    width: 1.2rem;
    height: 1.2rem;
    display: inline-flex;
    display: -webkit-inline-flex;
    align-items: center;
    justify-content: center;
    border-radius: 0.2rem;
    margin-bottom: 0.2rem;
  }

  .grids-container >>> .grids-item-container .grids-item {
    width: 100%;
    display: inline-flex;
    display: -webkit-inline-flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .grids-container >>> .grids-item-container:not(:first-child) {
    margin-top: .5rem;
  }

  .grids-container >>> .grids-item-container {
    width: 100%;
    display: flex;
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    justify-content: space-around;
  }

  .grids-container {
    margin-top: .5rem;
  }
</style>



